<template>
  <div class="blackboardWriting">
    <head-top title="板书"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <div class="list">
        <div v-for="(item, index) in dataList" :key="index" class="item">
          <div class="bg">
            <img :src="item.img" @click="preview(item.img)" />
          </div>
          <div class="time">
            <div class="info">{{item['time'] | formatDate(item['time']) }}</div>
          </div>
        </div>
        <div v-if="dataList.length % 2 === 1" class="item_empty"></div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { ImagePreview } from "vant";

export default {
  name: "",
  components: {
    headTop
  },
  props: {},
  data() {
    return {
      dataList: [],
      isLoading: false,
      imagePreview: null
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];

      let reqData = { courseID: this.$store.getters.courseID };
      this.$mainHttp.boardNote
        .getList(reqData)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push({
                img: this.$mainHttp.resourse.picture(item.BNContent, 4),
                state: item.State,
                title: item.BNTitle,
                time: item.CreateTime,
                id: item.BNID,
                content: item.BNContent
              });
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    preview(img) {
      this.imagePreview = ImagePreview({
        images: [img],
        showIndex: false,
        onClose() {}
      });
    }
  },
  destroyed() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang='less' scoped>
.blackboardWriting {
  .list {
    width: 100vw;
    // height: 375px;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    padding-bottom: 2vh;
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .item {
      // height: 10rem;
      width: calc(50vw - 1.5rem);
      border: 1px solid #ddd;
      margin: 1rem auto;
      text-align: center;
      background-color: rgba(0, 0, 0, 0.2);
      // overflow: hidden;

      &:after {
        content: "";
        flex: auto;
      }
      .bg {
        margin: 1rem;
        text-align: center;
        img {
          max-width: calc(50vw - 2.5rem);
          max-height: 8rem;
        }
      }
      .time {
        margin: 0.5rem 0;
        font-size: 1.2rem;
        color: rgba(102, 102, 102, 1);
      }
    }
    .item_empty {
      width: calc(50vw - 1.5rem);
      border: 0;
      margin: 1rem auto;
      text-align: center;
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem);
    }
  }
}
</style>


